import { FC, useContext, useEffect } from 'react';
import useFetchHubSpotForms from '../../hooks/useFetchHubSpotForms';
import { AppContext } from '../common/Layout/Layout';
import React from 'react';
import { ClipLoader } from 'react-spinners';
import { colors } from '../../utils/colors';
import { Modal } from '../common/Modal';
import { FormContainer } from './styled';

type Props = {
  onClose: () => void;
  messagingVolume: string;
  montlyPotentialLostRevenue: string;
  annualPotentialLostRevenue: string;
  platformFee: string;
  onSubmit: () => void;
};

export const CalculatorUserForm: FC<Props> = (props) => {
  const {
    onClose,
    messagingVolume,
    montlyPotentialLostRevenue,
    annualPotentialLostRevenue,
    platformFee,
    onSubmit
  } = props;
  const appContext = useContext(AppContext);

  useFetchHubSpotForms(
    [appContext.hubSpot.isHubSpotFormsLoaded],
    !appContext.hubSpot.isHubSpotFormsLoaded,
    appContext.hubSpot.setIsHubSpotFormsLoaded,
  );

  console.log(platformFee);

  useEffect(() => {
    //@ts-ignore
    window.hbspt?.forms.create({
      region: 'na1',
      portalId: '21630623',
      formId: '7662c7a2-2407-4e7e-87c7-0c3226c25e36',
      target: '#hubSpotCalculatorForm',
      onFormSubmit: function () {
        //@ts-ignore
        gtag_report_conversion();
        setTimeout(() => {
            onClose();
            onSubmit();
        }, 2000)
      },
      onFormReady: function (form: Element) {
        const gclidField = form.querySelector('input[name="gclid_field"]');
        if (gclidField) {
          gclidField.id = 'gclid_field';
        }

        const messagingVolumeField = form.querySelector(
          'input[name="smart_queue_calculator_volume"]',
        ) as HTMLInputElement;

        if (messagingVolumeField) {
          messagingVolumeField.value = messagingVolume;
        }

        const annualPotentialLostRevenueField = form.querySelector(
          'input[name="annual_potential_lost_revenue__sqc_"]',
        ) as HTMLInputElement;

        if (annualPotentialLostRevenueField) {
            annualPotentialLostRevenueField.value = annualPotentialLostRevenue;
        }

        const montlyPotentialLostRevenueField = form.querySelector(
          'input[name="monthly_potential_lost_revenue__sqc_"]',
        ) as HTMLInputElement;

        if (montlyPotentialLostRevenueField) {
          montlyPotentialLostRevenueField.value = montlyPotentialLostRevenue;
        }

        const platformFeeField = form.querySelector(
            'input[name="smart_queueing_calculator_platform_fees_cost"]',
          ) as HTMLInputElement;
  
          if (platformFeeField) {
            platformFeeField.value = platformFee;
          }
      },
    });
  }, [appContext.hubSpot.isHubSpotFormsLoaded]);

  return (
    <Modal onClose={onClose} showCloseButton>
      <FormContainer id="hubSpotCalculatorForm">
        {!appContext.hubSpot.isHubSpotFormsLoaded && (
          <ClipLoader color={colors.primary.lightOrange} />
        )}
      </FormContainer>
    </Modal>
  );
};
